export class CommonCanvas {
  canvas: HTMLCanvasElement
  ctx: CanvasRenderingContext2D
  boundMouseMove: (event: MouseEvent) => void
  boundResizeCanvas: () => void
  constructor(canvas: HTMLCanvasElement) {
    this.canvas = canvas
    this.ctx = this.canvas.getContext('2d', {
      willReadFrequently: true
    }) as CanvasRenderingContext2D
    this.boundMouseMove = this.mousePosition.bind(this)
    this.boundResizeCanvas = this.resizeCanvas.bind(this)
    window.addEventListener('resize', this.boundResizeCanvas)
    this.canvas.addEventListener('mousemove', this.boundMouseMove)
  }

  public resizeCanvas() {
    this.canvas.width = window.innerWidth
    this.canvas.height = window.innerHeight
    // console.log('Resized canvas to', this.canvas.width, this.canvas.height)
  }

  public clear() {
    this.ctx.clearRect(0, 0, this.canvas.width, this.canvas.height)
  }

  private mousePosition(event: MouseEvent) {
    const rect = this.canvas.getBoundingClientRect()
    const position = {
      x: event.clientX - rect.left,
      y: event.clientY - rect.top
    }
    // console.log(position) // Log the position
    return position
  }

  public destroy() {
    this.canvas.removeEventListener('mousemove', this.boundMouseMove)
    window.removeEventListener('resize', this.boundResizeCanvas)
  }
}
