import SideBar from './components/navigation/SideBar.vue';
import ChapterScroll from './components/navigation/ChapterScroll.vue';

const currentSection = ref('');
const expandedState = ref(false); // A new ref to store the state of is_expanded from ChapterScroll

const handleExpanded = (isExpanded) => {
  expandedState.value = isExpanded;
}
