import { CommonCanvas } from './CommonCanvas'
import { Mouse } from './ObctLits'
import { Candlestick, VolumeBar } from './SticksBars'
export class Navbar extends CommonCanvas {
  w: number
  h: number
  candlesticks: Candlestick[] = []
  volumeBars: VolumeBar[] = []
  mouseTrail: { x: number | undefined; y: number | undefined }[] = []
  marginRight = 50
  marginBottom = 20

  mouse: Mouse = {
    x: undefined,
    y: undefined,
    lastX: undefined,
    lastY: undefined,
    moved: false
  }
  movingDirection: String
  horizontalMove: String

  constructor(canvas: HTMLCanvasElement) {
    super(canvas)
    this.w = 0 // Initialize w
    this.h = 0 // Initialize h
    this.movingDirection = 'none' // 'up', 'down', 'none'
    this.horizontalMove = 'none'
    this.resizeCanvas()
  }
  public resizeCanvas = () => {
    super.resizeCanvas()
    this.w = this.canvas.width - this.marginRight // Adjusted width for drawing area
    this.h = this.canvas.height - this.marginBottom
  }

  public init() {
    this.resizeCanvas()
    this.animationLoop()
  }

  public mousemove = (e: MouseEvent) => {
    const expectedGap = 6
    this.mouse.lastX = this.mouse.x || e.clientX
    this.mouse.lastY = this.mouse.y || e.clientY
    this.mouse.x = e.clientX
    this.mouse.y = e.clientY
    this.mouse.moved = true
    if (this.mouse.y && this.mouse.lastY) {
      this.movingDirection =
        this.mouse.y < this.mouse.lastY
          ? 'up'
          : this.mouse.y > this.mouse.lastY
          ? 'down'
          : 'none'
    }
    if (this.mouse.x && this.mouse.lastX) {
      this.horizontalMove =
        this.mouse.x < this.mouse.lastX
          ? 'left'
          : this.mouse.x > this.mouse.lastX
          ? 'right'
          : 'none'
    }
    this.horizontalMove == 'right'
      ? this.mouseTrail.push({ x: this.mouse.x, y: this.mouse.y })
      : (this.mouseTrail = [])

    if (this.mouseTrail.length > 50) {
      // Adjust the number to control the length of the trail
      this.mouseTrail.shift()
    }

    let newX = this.mouse.x
    if (this.candlesticks.length > 0) {
      const lastCandlestick = this.candlesticks[this.candlesticks.length - 1]
      // const expectedGap = 3; // The desired gap between this.candlesticks
      // Adjust newX to ensure a gap of 3px from the last candlestick, accounting for direction
      if (this.mouse.x > lastCandlestick.x) {
        // Moving right
        newX = Math.max(
          newX,
          lastCandlestick.x + lastCandlestick.width + expectedGap
        )
      } else {
        // Moving left
        newX = Math.min(
          newX,
          lastCandlestick.x - lastCandlestick.width - expectedGap
        )
      }
    }

    if (
      !this.candlesticks.length ||
      (Math.abs(newX - this.mouse.lastX) >= expectedGap &&
        this.horizontalMove == 'right')
    ) {
      const candlestick = new Candlestick(newX, this.mouse.y, this.canvas, this)
      this.candlesticks.push(candlestick)

      // Determine the height of the volume bar, for example based on the height of the candlestick
      if (candlestick.height !== undefined) {
        const barHeight = candlestick.height / 3

        // Create a volume bar with the same 'newX' and color as the candlestick
        const volumeBar = new VolumeBar(
          newX,
          barHeight,
          candlestick.color,
          this.canvas,
          this
        )

        // Assuming you have a global array to store volume bars
        this.volumeBars.push(volumeBar)
      }
    }
  }

  public animationLoop = () => {
    this.ctx.clearRect(0, 0, this.w, this.h)
    this.ctx.globalCompositeOperation = 'source-over'
    // Draw this.candlesticks and volume bars
    if (this.mouse.x && this.mouse.lastX) {
      this.horizontalMove =
        this.mouse.x < this.mouse.lastX
          ? 'left'
          : this.mouse.x > this.mouse.lastX
          ? 'right'
          : 'none'
    }
    if (this.horizontalMove == 'right') {
      this.drawGrid()
      this.drawCrosshair()
      this.drawDateLabels()
      this.drawPriceLabels() // Add this line to draw the price labels
      this.drawTrail()
      this.drawCandlesticks()
      this.drawVolumeBars()
    } else if (this.horizontalMove == 'left') {
      // Clear the canvas or specific areas if you want to remove only labels and margins
      this.ctx.clearRect(this.w, 0, this.marginRight, this.h)
      this.ctx.clearRect(0, this.h, this.canvas.width, this.marginBottom)
    }
    // Remove expired this.candlesticks and their corresponding volume bars

    for (let i = this.candlesticks.length - 1; i >= 0; i--) {
      if (this.candlesticks[i] && this.volumeBars[i]) {
        this.candlesticks[i].update() // Update the time for candlestick
        this.volumeBars[i].update() // Update the time for volume bar in sync

        if (this.candlesticks[i].time > this.candlesticks[i].ttl) {
          // Remove the candlestick and its corresponding volume bar when ttl is exceeded
          this.candlesticks.splice(i, 1)
          this.volumeBars.splice(i, 1)
        }
      }
    }

    requestAnimationFrame(this.animationLoop)
  }

  public drawGrid() {
    const gridSize = 100 // Size for both width and height of the grid cells
    this.ctx.save() // Save the current state of the context
    this.ctx.globalAlpha = 0.5 // Set the transparency for the grid lines (adjust as needed)
    this.ctx.beginPath()
    this.ctx.strokeStyle = '#9da6a5' // Color of the grid lines
    this.ctx.lineWidth = 1 // Thickness of the grid lines

    // Drawing vertical lines
    for (let x = 0; x <= this.canvas.width; x += gridSize) {
      this.ctx.moveTo(x, 0)
      this.ctx.lineTo(x, this.canvas.height)
    }
    // Drawing horizontal lines
    for (let y = 0; y <= this.canvas.height; y += gridSize) {
      this.ctx.moveTo(0, y)
      this.ctx.lineTo(this.canvas.width, y)
    }

    this.ctx.stroke()
    this.ctx.restore() // Restore the context to its original state
  }

  public drawCrosshair() {
    if (this.mouse.x === undefined || this.mouse.y === undefined) {
      return // Don't draw if the this.mouse position is not defined
    }
    if (this.mouse.lastX) {
      this.horizontalMove =
        this.mouse.x < this.mouse.lastX
          ? 'left'
          : this.mouse.x > this.mouse.lastX
          ? 'right'
          : 'none'
    }
    const dashPattern = [5, 5] // Pattern for dashes and spaces: 5 pixels dashed, 5 pixels space

    // Draw vertical line
    this.ctx.beginPath()
    this.ctx.setLineDash(dashPattern) // Set the dash pattern for the line
    this.ctx.moveTo(this.mouse.x, 0) // Start at the top of the canvas at the current this.mouse x position
    this.ctx.lineTo(this.mouse.x, this.h) // Draw to the bottom of the canvas
    this.ctx.strokeStyle = '#FFFFFF' // Set the color of the crosshair
    this.horizontalMove === 'right'
      ? this.ctx.stroke()
      : this.ctx.setLineDash([]) // Draw the line if moving right, otherwise reset the dash pattern

    // Draw horizontal line
    this.ctx.beginPath()
    this.ctx.moveTo(0, this.mouse.y) // Start at the left of the canvas at the current this.mouse y position
    this.ctx.lineTo(this.w, this.mouse.y) // Draw to the right of the canvas
    this.horizontalMove === 'right'
      ? this.ctx.stroke()
      : this.ctx.setLineDash([]) // Draw the line if moving right, otherwise reset the dash pattern

    // Reset the dash pattern to solid after drawing
    this.ctx.setLineDash([])
  }

  public drawDateLabels() {
    const labelCount = 5 // Number of labels
    const step = this.w / labelCount

    this.ctx.fillStyle = '#FFFFFF' // Text color
    this.ctx.font = '10px Arial' // Text font and size
    this.ctx.textAlign = 'center'
    this.ctx.textBaseline = 'top' // Align text at the top

    for (let i = 0; i <= labelCount; i++) {
      const dateLabel = `Mar ${1 + i * 5}` // Example date label, should be dynamic
      const xPos = step * i // x position based on step
      this.ctx.fillText(dateLabel, xPos, this.h + 5) // A little offset below the bottom
    }
  }

  public drawPriceLabels() {
    const labelCount = 10 // Number of labels
    const priceRange = 100 // This should be dynamic based on your data range
    const step = this.h / labelCount

    this.ctx.fillStyle = '#FFFFFF' // Text color
    this.ctx.font = '10px Arial' // Text font and size
    this.ctx.textAlign = 'right'
    this.ctx.textBaseline = 'middle' // Center text vertically

    for (let i = 0; i <= labelCount; i++) {
      const priceLabel = ((priceRange / labelCount) * i).toFixed(2) // Calculate label
      const yPos = this.h - step * i // y position based on step
      this.ctx.fillText(priceLabel, this.w + this.marginRight / 2, yPos)
    }
  }
  public drawTrail() {
    if (this.mouseTrail.length < 2) {
      return // Not enough points to draw a line
    }

    // Define the offset distance for the parallel lines
    const offsetDistance = 100 // Adjust as needed for the desired effect

    // Function to draw a line with a given offset
    const drawOffsetLine = (
      offsetX: number,
      offsetY: number,
      color: string
    ) => {
      this.ctx.beginPath()
      if (
        this.mouseTrail[0].x !== undefined &&
        this.mouseTrail[0].y !== undefined
      ) {
        this.ctx.moveTo(
          this.mouseTrail[0].x + offsetX,
          this.mouseTrail[0].y + offsetY
        )
      }

      for (let i = 1; i < this.mouseTrail.length; i++) {
        // Use the non-null assertion operator (!) to tell TypeScript that this will not be null/undefined
        const trail = this.mouseTrail[i]

        if (trail.x !== undefined && trail.y !== undefined) {
          this.ctx.lineTo(trail.x + offsetX, trail.y + offsetY)
        }
      }

      this.ctx.strokeStyle = color
      this.ctx.lineWidth = 1 // Adjust as needed
      this.ctx.stroke()
    }
    // Draw three lines: one centered, and one each above and below the center line
    drawOffsetLine(0, 0, 'red') // Center line
    drawOffsetLine(0, -offsetDistance, '#2745d9') // Upper line
    drawOffsetLine(0, offsetDistance, '#2745d9') // Lower line
  }

  public drawCandlesticks() {
    for (let i = 0; i < this.candlesticks.length; i++) {
      this.candlesticks[i].update()
      this.candlesticks[i].draw()
    }
  }
  public drawVolumeBars() {
    for (let i = 0; i < this.volumeBars.length; i++) {
      this.volumeBars[i].draw()
    }
  }

  public mouseout = () => {
    this.mouse.x = undefined
    this.mouse.y = undefined
  }

  public calculateHeight() {
    if (this.mouse.x && this.mouse.lastX && this.mouse.y && this.mouse.lastY) {
      const dx = Math.abs(this.mouse.x - this.mouse.lastX) // Absolute change in x
      const dy = Math.abs(this.mouse.y - this.mouse.lastY) // Absolute change in y
      const baseHeight = 30 // Minimum height for horizontal movement

      // Ensure we don't divide by zero; if dx is very small (indicating almost vertical movement), amplify the effect
      const ratio = dx === 0 ? 10 : dy / dx // Use a high ratio if dx is zero to maximize height for vertical movement

      // Calculate height as baseHeight plus an additional component based on the dy/dx ratio
      // Clamp the maximum height to a reasonable value to prevent excessively tall candlesticks
      const calculatedHeight = Math.min(baseHeight + ratio * 50, 1000) // Adjust multiplier as needed to tune the sensitivity

      return calculatedHeight
    }
  }
}
