import { CommonCanvas } from './CommonCanvas'
import { Particle } from './Particle'

export class Effect extends CommonCanvas {
  public canvasWidth: number
  public canvasHeight: number
  public textX: number
  public textY: number
  public context: CanvasRenderingContext2D // Add the context property
  public fontSize: number
  private maxTextWidth: number
  private lineHeight: number
  private verticalOffest: number
  private particles: any[]
  public gap: number
  public mouse: { radius: number; x: number; y: number }
  private pixels: any

  constructor(canvas: HTMLCanvasElement) {
    super(canvas)
    this.resizeCanvas()
    this.context = this.ctx
    this.canvasWidth = this.canvas.width
    this.canvasHeight = this.canvas.height
    this.textX = this.canvasWidth / 2
    this.textY = this.canvasHeight / 2
    this.fontSize = 100
    this.lineHeight = this.fontSize * 1
    this.maxTextWidth = this.canvasWidth * 0.8
    this.verticalOffest = -100
    this.particles = []
    this.gap = 3
    this.mouse = {
      radius: 20000,
      x: 0,
      y: 0
    }
    window.addEventListener('mousemove', (e) => {
      this.mouse.x = e.x
      this.mouse.y = e.y
    })
  }

  wrapText(text: string) {
    // Canvas Settings
    const gradient = this.context.createLinearGradient(
      0,
      0,
      this.canvasWidth,
      this.canvasHeight
    )
    gradient.addColorStop(0.3, 'white')
    gradient.addColorStop(0.5, 'blue')
    gradient.addColorStop(0.7, 'purple')
    this.context.fillStyle = gradient
    this.context.textAlign = 'center'
    this.context.textBaseline = 'middle'
    this.context.lineWidth = 3
    this.context.strokeStyle = 'aqua'
    this.context.font = this.fontSize + 'px Courier New'
    // Break Multi-line Text
    const linesArray = []
    const words = text.split(' ')
    let lineCounter = 0
    let line = ''
    for (let i = 0; i < words.length; i++) {
      const testLine = line + words[i] + ' '
      if (this.context.measureText(testLine).width > this.maxTextWidth) {
        line = words[i] + ' '
        lineCounter++
      } else {
        line = testLine
      }
      linesArray[lineCounter] = line
    }

    const textHeight = this.lineHeight * lineCounter
    this.textY = this.canvasHeight / 2 - textHeight / 2 + this.verticalOffest
    linesArray.forEach((el, index) => {
      this.context.fillText(
        el,
        this.textX,
        this.textY + index * this.lineHeight
      )
      this.context.strokeText(
        el,
        this.textX,
        this.textY + index * this.lineHeight
      )
    })
    this.convertToParticles()
  } // end of wrapText

  convertToParticles() {
    this.particles = []
    this.pixels = this.context.getImageData(
      0,
      0,
      this.canvasWidth,
      this.canvasHeight
    ).data
    this.context.clearRect(0, 0, this.canvasWidth, this.canvasHeight)
    for (let y = 0; y < this.canvasHeight; y += this.gap) {
      for (let x = 0; x < this.canvasWidth; x += this.gap) {
        const index = (y * this.canvasWidth + x) * 4
        const alpha = this.pixels[index + 3]
        if (alpha > 0) {
          const red = this.pixels[index]
          const green = this.pixels[index + 1]
          const blue = this.pixels[index + 2]
          const color = 'rgb(' + red + ', ' + green + ', ' + blue + ')'
          this.particles.push(new Particle(this, x, y, color))
        }
      } // end forloop x
    } // end forloop y
  } // end of convertToParticles

  render() {
    this.particles.forEach((particle) => {
      particle.update()
      particle.draw()
    })
  }

  resize(width: number, height: number) {
    this.canvasWidth = width
    this.canvasHeight = height
    this.textX = this.canvasWidth / 2
    this.textY = this.canvasHeight / 2
    this.maxTextWidth = this.canvasWidth * 0.8
  }
}

