<script lang="ts">
import { ref, onMounted, onUnmounted, reactive, toRefs, watch, nextTick } from 'vue';
import { Effect } from '../components/canvas/Effect';
import { Navbar } from '../components/canvas/Navbar';

export default {
    setup() {
        const canvasRef = ref<HTMLCanvasElement | null>(null);
        const candlesRef = ref<HTMLCanvasElement | null>(null);
        const state = reactive({
            isNavbarVisible: false,
        });
        let particles: Effect | null = null;
        let navCandles: Navbar | null = null;
        let animationFrameId: number | null = null;
        const text: string = "QUANTUM TRADING SYSTEM 24";

        const toggleNavbar = () => {
            state.isNavbarVisible = !state.isNavbarVisible;
        };

        const animate = () => {
            if (canvasRef.value && particles) {
                particles.clear();
                particles.render();
                animationFrameId = requestAnimationFrame(animate);
            }
        };

        onMounted(() => {
            if (canvasRef.value) {
                const canvas: HTMLCanvasElement = canvasRef.value;
                canvas.width = window.innerWidth;
                canvas.height = window.innerHeight;
                particles = new Effect(canvas);
                particles.resize(canvas.width, canvas.height);
                particles.wrapText(text);
                animate();
                window.addEventListener('resize', () => {
                    canvas.width = window.innerWidth;
                    canvas.height = window.innerHeight;
                    if (particles) {
                        particles.resize(canvas.width, canvas.height);
                        particles.wrapText(text);
                    }
                    if (particles) {
                        switch (true) {
                            case window.innerWidth < 500:
                                particles.fontSize = 80;
                                break;
                            case window.innerWidth > 900:
                                particles.fontSize = 100;
                                break;
                            default:
                                // Handle other cases if needed
                                break;
                        }
                    }
                });
            }
        });

        // Watching isNavbarVisible to initialize or cleanup navCandles
        watch(() => state.isNavbarVisible, async (newVal) => {
            if (newVal) {
                // Wait for the next DOM update cycle to complete
                await nextTick();
                if (candlesRef.value) {
                    // Now that the DOM has updated, the canvas element should be available
                    const candles: HTMLCanvasElement = candlesRef.value;
                    candles.width = window.innerWidth;
                    candles.height = window.innerHeight;
                    navCandles = new Navbar(candles);
                    navCandles.init();
                    window.addEventListener('resize', navCandles.resizeCanvas)
                    window.addEventListener('mousemove', navCandles.mousemove)
                    window.addEventListener('mouseout', navCandles.mouseout)

                }
            } else {
                // Cleanup or additional actions when navbar is hidden
                navCandles = null;
            }
        });

        onUnmounted(() => {
            if (animationFrameId !== null) {
                cancelAnimationFrame(animationFrameId);
            }
            if (particles) {
                particles.destroy();
            }
        });

        return { ...toRefs(state), canvasRef, toggleNavbar, candlesRef };
    }
};

</script>
