import { CommonCanvas } from './CommonCanvas'
import type { Navbar } from './Navbar'

export class Candlestick extends CommonCanvas {
  navbar: Navbar
  x: number
  y: number
  width: number
  height: number | undefined
  wickOffset: number
  color: string
  time: number
  ttl: number

  constructor(
    newX: number,
    newY: number,
    canvas: HTMLCanvasElement,
    navbar: Navbar
  ) {
    super(canvas)
    this.navbar = navbar
    this.x = newX
    this.y = newY
    this.width = 8
    this.height = this.navbar.calculateHeight()
    this.wickOffset = 10
    this.color = this.navbar.movingDirection === 'up' ? 'green' : 'red'
    this.time = 0
    this.ttl = 60
  }
  //
  public draw() {
    this.ctx.fillStyle = this.color
    if (this.height) {
      this.ctx.fillRect(
        this.x - this.width / 2,
        this.y - this.height / 2,
        this.width,
        this.height
      )

      // Adjusted wick length to be 1/3 of the candlestick's height
      const wickLength = this.height / 3
      this.ctx.beginPath()
      this.ctx.moveTo(this.x, this.y - this.height / 2 - wickLength) // Adjust for wick above
      this.ctx.lineTo(this.x, this.y + this.height / 2 + wickLength) // Adjust for wick below
      this.ctx.strokeStyle = this.color
      this.ctx.stroke()
    }
  }

  public update = () => {
    this.time++
    if (!this.navbar.mouse.moved) {
      this.ttl -= 2 // Accelerate disappearance if mouse is not moving
    }
  }
}

export class VolumeBar extends CommonCanvas {
  navbar: Navbar
  x: number
  y: number
  width: number
  height: number
  color: string
  time: number
  ttl: number
  constructor(
    newX: number,
    barHeight: number,
    color: string,
    canvas: HTMLCanvasElement,
    navbar: Navbar
  ) {
    super(canvas)
    this.navbar = navbar
    this.x = newX
    this.y = navbar.h // Assuming 'h' is the height of your canvas and you want the volume bars to be at the bottom
    this.width = 8 // Same width as candlestick for consistency, you can adjust as needed
    this.height = barHeight // Height of the volume bar
    this.color = color // Color of the bar, matching the candlestick
    this.time = 0
    this.ttl = 60
  }

  public draw() {
    this.ctx.fillStyle = this.color
    this.ctx.fillRect(
      this.x - this.width / 2,
      this.y - this.height,
      this.width,
      this.height
    )
  }

  public update = () => {
    this.time++
    if (!this.navbar.mouse.moved) {
      this.ttl -= 2 // Accelerate disappearance if mouse is not moving
    }
  }
}
