<template>
    <aside id="scroll" :class="`${is_expanded ? 'is-expanded' : ''}`">
        <div class="menu-toggle-wrap">
            <button class="menu-toggle" @click="ToggleMenu">
                <span class="material-icons">keyboard_double_arrow_right</span>
            </button>
        </div>
        <h3>Chapters</h3>
        <div class="chapter">Chapters</div>
        <div class="menu">
            <!-- Directly use headers and currentSection props in the template -->
            <a v-for="(header, index) in headers" :key="index" :href="`#section-${index}`"
                :class="{ active: `section-${index}` === currentSection }">
                {{ header }}
            </a>
        </div>
    </aside>
</template>
<script setup lang="ts">
import { defineProps, ref, defineEmits } from 'vue';

const emits = defineEmits(['updateExpanded']);
const is_expanded = ref(false);

const ToggleMenu = () => {
    is_expanded.value = !is_expanded.value;
    emits('updateExpanded', is_expanded.value); // Emit the current state of is_expanded
}

defineProps<{
    headers: string[];
    currentSection?: string;
}>();
</script>


